export default {
	data() {
		return {
			oldpwd : "",
			newpwd : "",
			aginpwd : "",
			useUrl:this.user
		}
	},
	onLoad(options) {
	    
	},
	methods: {
		// 显示密码的函数
		showpwd1 : function(){
			this.type1 = "text";
		},
		showpwd2 : function(){
			this.type2 = "text";
		},
		showpwd3 : function(){
			this.type3 = "text";
		},
		xg : function(){
			if(this.oldpwd == "" || this.newpwd == "" || this.aginpwd == ""){
				uni.showToast({
					title : "请完整的填写信息！",
					icon : "none"
				})
			}else if(this.newpwd != this.aginpwd){
				uni.showToast({
					title : "两次密码不一致！",
					icon : "none"
				})
			}else if(this.oldpwd!=this.useUrl.pd){
				uni.showToast({
					title : "现密码和原密码不符合！",
					icon : "none"
				})
			}else{
				let that=this;
				let usurl1=that.url1+"/dinghai/user/update/Password";
				uni.request({
				    url: usurl1,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
				    data: {
						uid:that.user.uid,
						password:that.newpwd
					},
				    success: (res) => {
						if(res.statusCode==200){
							uni.showToast({
								title:'修改成功'
							})
							uni.navigateTo({
								url : "../../../login/index/index"
							});
						}
				    },
					fail : function(err){
						console.log(err);
					}
				})
			}
		},
		back:function(){
			uni.navigateBack({
				
			})
		}	
	}
}
